import { Box, Button, Card, Grid, styled, TextField } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Avatar from "@mui/material/Avatar";
import Typography from "@mui/material/Typography";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";

import { api } from "../../../api/API";
import SimpleDialog from "../../dialogs/SimpleDialog";
const FlexBox = styled(Box)(() => ({
  display: "flex",
  alignItems: "center",
}));

const JustifyBox = styled(FlexBox)(() => ({
  justifyContent: "center",
}));

const ContentBox = styled(Box)(({ theme }) => ({
  padding: 32,
  background: theme.palette.background.default,
}));

const ForgotPasswordRoot = styled(JustifyBox)(() => ({
  background: "#1A2038",
  minHeight: "100vh !important",
  "& .card": {
    maxWidth: 800,
    margin: "1rem",
    borderRadius: 12,
  },
}));

const ForgotPassword = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [showPrompt, setShowPrompt] = useState(false);
  const [message, setMessage] = useState("");
  const handleFormSubmit = async (e) => {
    e.preventDefault();

    await api
      .post("forgot-password-email", {
        body: {
          userEmail: email,
        },
      })
      .then((res) => {
        if (res.success) {
          setMessage("Reset password sent to your email id successfully");
          setShowPrompt(true);
        } else {
          setMessage("User not found. Please sign up.");
          setShowPrompt(true);
        }
      });
  };

  return (
    <ForgotPasswordRoot
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        backgroundImage: `url(${"/assets/images/illustrations/transparant.PNG"})`,
        backgroundColor: "#263238",
        backgroundSize: "cover",
        height: "100vh",
        justifyContent: "center",
      }}
    >
      <Card
        sx={{
          border: "3px solid",
          color: "#cfd8dc",
          marginRight: "5%",
          mb: 8,
          maxWidth: "450px",
          marginBottom: "7%",
        }}
      >
        <Grid sx={{ mx: 2, my: 4 }}>
          <Avatar sx={{ m: 1, bgcolor: "#aa00ff", marginLeft: "43%" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography
            alignItems={"center"}
            component="h1"
            variant="h5"
            sx={{ marginLeft: "27%", mb: 6, color: "#263238" }}
          >
            Forgot password
          </Typography>
          <form onSubmit={handleFormSubmit} method="post">
            <TextField
              type="email"
              name="email"
              size="small"
              label="Email"
              value={email}
              variant="outlined"
              onChange={(e) => setEmail(e.target.value)}
              sx={{ mb: 3, width: "100%" }}
            />

            <Button fullWidth variant="contained" color="primary" type="submit">
              Reset Password
            </Button>

            <Button
              fullWidth
              color="primary"
              variant="outlined"
              onClick={() => navigate(-1)}
              sx={{ mt: 2 }}
            >
              Go Back
            </Button>
          </form>
        </Grid>
      </Card>
      {showPrompt && (
        <SimpleDialog
          show={showPrompt}
          description={message}
          title="Reset Password"
          buttonText="Close"
          onClose={() => {
            setShowPrompt(false);
          }}
        />
      )}
    </ForgotPasswordRoot>
  );
};

export default ForgotPassword;
